import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { useRecoilState } from "recoil";
import styled from "styled-components";
import { memoState } from "./atom";
import Board from "./Components/Board";

const Wrapper = styled.div`
  display: flex;
  max-width: 480px;
  width: 100%;
  margin: 10px auto;
  justify-content: center;
  align-items: center;
  height: 100vh;
`;

const Boards = styled.div`
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(3, 1fr);
  /* height: 100vh; */
`;

function App() {
  const [memos, setMemos] = useRecoilState(memoState);

  //        
  const onDragEnd = (info: DropResult) => {
    const { destination, source } = info;
    if (!destination) return;
    if (destination?.droppableId === source.droppableId) {
      setMemos((allBoards) => {
        //   [...a[b]]
        // ...allBoards : default   
        // source.droppableId : default     ()
        //   :       =>     X
        // ...allBoards     []     ~
        const boardCopy = [...allBoards[source.droppableId]];
        const taskObj = boardCopy[source.index];

        //   
        // draggableId  x O
        boardCopy.splice(source.index, 1);
        boardCopy.splice(destination?.index, 0, taskObj);
        return {
          //    board
          ...allBoards,

          //   .
          // [key] :  (todo,doing,done)
          // boardCopy:  
          [source.droppableId]: boardCopy,
        };
      });
    }
    if (destination.droppableId !== source.droppableId) {
      setMemos((allBoards) => {
        const sourceBoard = [...allBoards[source.droppableId]];
        const taskObj = sourceBoard[source.index];
        const destBoard = [...allBoards[destination.droppableId]];
        sourceBoard.splice(source.index, 1);
        destBoard.splice(destination?.index, 0, taskObj);
        return {
          ...allBoards,
          [source.droppableId]: sourceBoard,
          [destination.droppableId]: destBoard,
        };
      });
    }
  };

  // li    index.tsx React.StrictMode
  // onDragEnd :      
  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Wrapper>
        {/* Object.keys(obj) :     ex) Object.keys(obj).map((item)=>obj[item])
            Object.keys(memos)    . (to_do,doing,done)
               map()  objName[  ]     
            ex) memos[boardId]   memos["to_do"]     
        */}
        <Boards>
          {Object.keys(memos).map((boardId) => (
            <Board boardId={boardId} key={boardId} memos={memos[boardId]} />
          ))}
        </Boards>
      </Wrapper>
    </DragDropContext>
  );
}

export default App;

