import { useForm } from "react-hook-form";
import { Droppable } from "react-beautiful-dnd";
import DraggableCard from "./DraggableCard";
import styled from "styled-components";
import { IMemo, memoState } from "../atom";
import { useSetRecoilState } from "recoil";

const Wrapper = styled.div`
  width: 300px;
  background: ${(prop) => prop.theme.boardColor};
  border-radius: 5px;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 500px;
  padding-top: 10px;
  border: 1px solid ${(prop) => prop.theme.boardColor};
`;

// styled component isDraggingOver prop   
const Area = styled.div<IAreaProps>`
  background-color: ${(prop) =>
    prop.isDraggingOver
      ? "#f6cadfa9"
      : prop.isDraggingFromThis
      ? "#fad0c9a2"
      : prop.theme.boardColor};
  flex-grow: 1;
  transition: 0.3s;
  padding: 5px 15px 15px 15px;
  overflow-y: auto;
  overflow-x: hidden;
`;

const Title = styled.div`
  text-align: center;
  font-weight: 600;
  padding: 15px;
  color: ${(prop) => prop.theme.bgColor};
  text-transform: uppercase;
`;

const Form = styled.form`
  display: flex;
  padding: 5px 15px 10px 15px;
  input {
    width: 100%;
    height: 25px;
    padding: 0 10px;
    /* border-radius: 5px; */
    border: none;
    border-bottom: 1px solid #a7a7a76a;
    background: none;
    outline: none;
  }
  button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 25px;
    color: #a7a7a76a;
  }
`;

interface IAreaProps {
  isDraggingOver: boolean;
  isDraggingFromThis: boolean;
}

interface IBoardProps {
  memos: IMemo[];
  boardId: string;
}

interface IForm {
  memo: string;
}

function Board({ memos, boardId }: IBoardProps) {
  const setMemoState = useSetRecoilState(memoState);
  const { register, setValue, handleSubmit } = useForm<IForm>();
  const onValid = ({ memo }: IForm) => {
    const newMemo = {
      id: Date.now(),
      text: memo,
    };
    setMemoState((allBoards) => {
      return {
        //  board +  board     retrun  
        //       state  
        ...allBoards, //   board
        [boardId]: [
          newMemo, //  board     
          ...allBoards[boardId],
        ],
      };
    });
    setValue("memo", "");
  };
  return (
    <Wrapper>
      <Title>{boardId}</Title>

      {/* ref : HTML     
          .current       
       */}
      <Form onSubmit={handleSubmit(onValid)}>
        <input
          {...register("memo", { required: true })}
          type="text"
          placeholder={" "}
        />
        <button>+</button>
      </Form>
      <Droppable droppableId={boardId}>
        {(provided, snapshot) => (
          // isDraggingOver :  board      
          // draggingFromThisWidth:   board    
          // innerRef, droppableProps : Droppable  
          // Droppable      
          <Area
            isDraggingOver={snapshot.isDraggingOver}
            isDraggingFromThis={Boolean(snapshot.draggingFromThisWith)}
            ref={provided.innerRef}
            {...provided.droppableProps}
          >
            {memos.map((memo, index) => (
              <DraggableCard
                key={memo.id}
                memoId={memo.id}
                memoText={memo.text}
                index={index}
              />
            ))}
            {provided.placeholder}
          </Area>
        )}
      </Droppable>
    </Wrapper>
  );
}

export default Board;
